import React, { useContext, useState } from 'react'
import { changeScssVariables } from '../services/changeScssVariables'
import { setLocalStorage } from '../services/localStorage'

export const THEME_DARK = 'dark'
export const THEME_LIGHT = 'light'
export const THEME_PURPULISH = 'purple'
export const THEME_REDDISH = 'red'
export const THEME_NEUTRAL = 'neutral'

const ThemeContext = React.createContext()

export const ThemeProvider = ({ children, ...props }) => {
  const [theme, setTheme] = useState(null)

  const change = (name) => {
    setTheme(name)
    changeScssVariables(name)
    setLocalStorage('theme', name)
  }

  return (
    <ThemeContext.Provider value={{ theme, change }} {...props}>
      {children}
    </ThemeContext.Provider>
  )
}

export default ThemeProvider

export const useTheme = () => useContext(ThemeContext)
